html,
body {
  height: 100%;
  margin: 0;
  padding: 0;
}

.content {
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #181818;
  color: white;
}

.title,
.total-repo-count,
#location {
  color: greenyellow;
}

.logo {
  height: 50px;
}

.logo > a:hover > .svg path {
  fill: royalblue;
}

.svg path {
  fill: #fff;
}
